<template>
    <div class="container">
        <template v-if="step===0">
            <div class="area-code-cell" @click="openAreaCode">
                <span class="area-name">{{selectObj.cn}}</span>
                <i class="ccyicon icon-arrow_right"/>
            </div>
            <div class="mobile-cell">
                <span class="area-code">+{{selectObj.key}}</span>
                <h5-input class="h5-input-mobile" type="text" clearable focus v-model="mobile" placeholder="请输入绑定的手机号"/>
            </div>
            <button type="button" :disabled="mobile===''" class="H5-btn H5-btn-primary next-step" @click="step1">下一步
            </button>
        </template>
        <template v-else-if="step===1">
            <div class="verify-tip">
                <img class="mobile-safe" src="/mobile-safe.png" mode="widthFix" alt=""/>
                <div class="tip-txt">
                    <div>为了您的账号安全，需要验证您的手机号</div>
                    <div>137****3011</div>
                </div>
            </div>
            <div class="verify-container">
                <h5-input class="h5-input-verify" type="text" clearable focus v-model="verify" placeholder="请输入验证码"/>
                <span class="verify-a"
                      :style="{color:verifyFrozenTime!==0?'#C0C0C0':'#FF6100'}"
                      @click="getVerify">
                    {{verifyFrozenTime===0?(verifyFrozenTimer?'重新获取':'点击获取'):verifyFrozenTime+'s后重试'}}
                </span>
            </div>
            <button type="button" :disabled="verify===''" class="H5-btn H5-btn-primary next-step" @click="step2">确认
            </button>
        </template>
        <template v-else-if="step===2">
            <alter-psw/>
        </template>
        <V5Dialog ref="v5dialog" :host="v5Cfg.host" :token="v5Cfg.token"/>
        <area-select ref="areaSelect" @recvObj="recvObj" :current="selectObj.index"/>
    </div>
</template>
<script>
    import alterPsw from './alter-psw';
    import {V5Dialog} from 'verify5-vue';
    import h5Input from '@/components/form/h5-input.vue'
    import areaSelect from '@/components/area-select/area-select';

    export default {
        name: "alterpsw-by-mobile",
        components: {
            alterPsw,
            areaSelect,
            V5Dialog,
            h5Input
        },
        data() {
            return {
                v5Cfg: {host: "", token: ""},
                mobile: "",
                verify: "",
                verifyFrozenTime: 0,
                selectObj: {
                    index: 0,
                    key: "86",
                    cn: "中国(+86)",
                    eng: "China (+86)"
                },
                verifyFrozenTimer: undefined,
                step: 0  //0填写手机号,1输入验证码,2修改密码
            }
        },
        created() {
            this.$util.request({
                url: "/customer/login/verify/token"
            }).then(res => {
                this.v5Cfg = {
                    host: this.$util.verify.host,
                    token: res ? res.data : "",
                    trustLevel: this.$util.verify.trustLevel,
                };
            }).catch(function (error) {
                console.error(error);
            });
            let username = this.$util.getUrlParam("username") || "";
            if (username.indexOf("@") === -1) {
                this.mobile = username;
            }
        },
        methods: {
            step1() {
                var reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/; //正则表达式
                if (!reg.test(this.mobile)) { //正则验证不通过，格式不对

                } else {
                    let that = this;
                    this.$refs.v5dialog.verify((result) => {
                        that.$util.verify.valid2nd(result, () => {
                            console.log("verify success");
                            this.step = 1
                        }, () => {
                            console.log("verify fail");
                        });
                    });
                }
            },
            recvObj(data) {
                console.log(data);
                this.selectObj = data;
            },
            openAreaCode() {
                this.$refs.areaSelect.toggleMask();
            },
            getVerify() {
                if (this.verifyFrozenTime !== 0) {
                    return;
                }
                //cooldown
                let that = this;
                this.verifyFrozenTime = 60;
                let time = setInterval(function () {
                    that.verifyFrozenTime = that.verifyFrozenTime - 1;
                    if (that.verifyFrozenTime === 0) {
                        clearInterval(time);
                        this.verifyFrozenTimer = undefined;
                    }
                }, 1000);
                this.verifyFrozenTimer = time;
            },
            step2() {
                if (this.verifyFrozenTimer) {
                    clearInterval(this.verifyFrozenTimer);
                }
                this.step = 2
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "~@/../node_modules/verify5-css/css/v5.css";

    .container {
        background-color: $page-color-base;
        padding-top: $spacing-col-base;
    }

    .mobile-input {
    }

    .next-step {
        margin-top: $spacing-col-base;
        margin-left: $spacing-row-base;
        margin-right: $spacing-row-base;
    }

    .area-code-cell {
        display: flex;
        background-color: white;
        padding: 16px 4px;
        align-items: center;
        border-bottom: $border-style-base;

        .area-name {
            flex: 1;
            margin-left: $spacing-row-base;
        }

        .ccyicon {
            margin-right: $spacing-row-base;
            font-size: $text-size-lg;
        }
    }

    .mobile-cell {
        display: flex;
        background-color: white;
        align-items: center;
        width: 100%;

        .area-code {
            text-align: center;
            flex: 1;
            color: $color-primary;
            border-right: $border-style-base;
        }

        .h5-input-mobile {
            flex: 5;
        }
    }

    .verify-container {
        display: flex;
        background-color: white;
        align-items: center;

        .h5-input-verify {
            flex: 7;
        }

        .verify-a {
            border-left: $border-style-base;
            text-align: center;
            flex: 3;
        }
    }

    .verify-tip {
        display: flex;
        align-items: center;
        flex-direction: column;

        .mobile-safe {
            width: $img-size-hg;
        }

        .tip-txt {
            text-align: center;
            font-size: $text-size-base;
            margin-top: $spacing-col-base;
            margin-bottom: $spacing-col-base;
        }
    }
</style>