<template>
    <!-- 底部分享弹窗 -->
    <div v-if="show" class="mask" @click="toggleMask" @touchmove.stop.prevent="stopPrevent">
        <div class="show-select">
            <div class="select-header">地区选择</div>
            <div scroll-y class="area-scroll-view">
                <root-form class="list-cell" ref="form" :model="form" :rules="rules" v-for="(item, index) in areaArr" :key="index" @click="handle">
                    <form-item class="radio-item" label="" border>
                        <radio v-model="curSelect" name="areaObj" :label="index">
                            {{item.cn+' (+'+item.key+')'}}
                        </radio>
                    </form-item>
                </root-form>
            </div>
            <div class="select-footer" @click="toggleMask">取消</div>
        </div>
    </div>
</template>

<script>
    import areaCode from './area-code.data.js';

    export default {
        components: {},
        props: {
            contentHeight: {
                type: Number,
                default: 0
            },
            current: {
                type: Number,
                default: 0
            },
        },
        data() {
            return {
                form: {},
                rules: {},
                curSelect: 1,
                show: false,
                timer: 0,
                config: {},
                areaArr: areaCode.data,
            }
        },
        created() {
            this.config = {
                height: this.contentHeight + 'px',
                transform: `translateY(${this.contentHeight})`,
                backgroundColor: 'rgba(0,0,0,.4)',
            };
            this.transform = this.config.transform;
            this.curSelect = this.current;
        },
        methods: {
            toggleMask() {
                //防止高频点击
                if (this.timer === 1) {
                    return;
                }
                this.timer = 1;
                setTimeout(() => {
                    this.timer = 0;
                }, 500);

                if (this.show) {
                    this.transform = this.config.transform;
                    this.backgroundColor = 'rgba(0,0,0,0)';
                    setTimeout(() => {
                        this.show = false;
                    }, 200);
                    this.$emit('recvObj', this.areaArr[this.curSelect]);
                    return;
                }

                this.show = true;
                //等待mask重绘完成执行
                setTimeout(() => {
                    this.backgroundColor = this.config.backgroundColor;
                    this.transform = 'translateY(0px)';
                }, 10)
            },
            stopPrevent() {
            },
            selectAreaCode(e) {
                let idx = Number(e.target.value);
                let data = this.areaArr[idx];
                data.index = idx;
                this.$emit('recvObj', data);
                this.toggleMask();
            },
            handle() {
                console.log("取到的值是" + this.selectStatus);
            }
        }
    }
</script>

<style lang='scss'>
    .mask {
        position: fixed;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1001;
        transition: .3s;
        background-color: $bg-color-mask;
    }

    .radio-item {
        display: block;
        margin-bottom: $spacing-col-sm !important;
    }

    .show-select {
        background-color: white;
        transition: .3s;
        width: 90vw;
        border-radius: $border-radius-base;

        .select-header {
            padding: $spacing-col-sm $spacing-row-base;
            font-size: $text-size-title;
            color: $color-title;
            border-bottom: solid $border-color-base thin;
        }

        .area-scroll-view {
            padding: $spacing-col-base $spacing-row-base;
            height: 400px;
            width: auto;
            overflow: scroll auto;
        }

        .select-footer {
            padding: $spacing-col-sm $spacing-row-base;
            color: $color-primary;
            text-align: right;
            border-top: solid $border-color-base thin;
        }

        .list-cell {
            label {
                display: flex;
                align-items: center;
            }

            text {
                padding-left: $spacing-row-sm;
            }
        }
    }
</style>
